.header {
  width: 100%;
  height: 5rem;
  display: flex;
  padding: 0 10%;
  justify-content: space-between;
  align-items: center;
  background-color: #336699;
}

.logo {
  font-size: 2rem;
  color: #ffc107;
}

.nav ul {
  list-style: none;
  display: flex;
  margin: 0;
  padding: 0;
}

.nav li {
  margin-left: 1.5rem;
  font-size: 1.25rem;
}

.nav a {
  text-decoration: none;
  color: #9e9e9e;
}

.nav a:hover,
.nav a:active,
.nav a.active {
  color: #ffffff;
}
